import React, { Component } from 'react'
import { FormStyle } from '../../styles/init';
import validator from 'validator';

/**
 * @param string width in px
 * @param string label 
 * @param string placeholder
 * @param string type
 * @param array options
 * @param function data() {}
 */

export default class Form extends Component {
    constructor(props) {
        super(props);
        this.state = {
            "data": props.type === "select" ? props.placeholder : "",
            "validator": false,
            "error_style": ""
        }
    }

    // componentDidUpdate(prevprops) {
    //     if(prevprops.value !== this.props.value) {
    //         this.setState({
    //             data: this.props.value
    //         })
    //     }
    // }

    getData = e =>{ 
        const data = e.target.value; 
        const { type } = this.props;
        const val = this.dataValidator(data,type);
        this.setState({ 
            "data": type === "checkbox" ? e.target.checked : data,
            "validator": val,
            "error_style": val ? null : `form_error`
        },()=>{ 
            const { data,validator } = this.state;
            this.props.data(data,validator); 
        });
    }

    dataValidator = (data,type) => { 
        const { placeholder } = this.props;
        if (type === "email") {
            return validator.isEmail(data);
        }else if (type === "text") {
            return !validator.isEmpty(data);
        }else if (type === "password") {
            return !validator.isEmpty(data);
        }else if (type === "select") {
            return !validator.equals(data,placeholder);
        }else {
            return undefined
        }
    }
    render() {
        const { width,label,type,placeholder,options } = this.props;
        const { error_style,data } = this.state;
        let view;
        switch(type) {
            case "text": 
                view = ( 
                    <FormStyle width={width} type={type}>
                        <label>{label}</label>
                        <input type={type} className={error_style} placeholder={placeholder} value={this.state.data} name={type} onChange={this.getData}/>
                    </FormStyle>
                );
            break;
            case "email": 
                view = ( 
                    <FormStyle width={width} type={type}>
                        <label>{label}</label>
                        <input type={type} className={error_style} placeholder={placeholder} value={this.state.data} name={type} onChange={this.getData}/>
                    </FormStyle>
                );
            break;
            case "password": 
                view = ( 
                    <FormStyle width={width} type={type}>
                        <label>{label}</label>
                        <input type={type} className={error_style} placeholder={placeholder} value={this.state.data} name={type} onChange={this.getData}/>
                    </FormStyle>
                );
            break;
            case "select": 
                view = ( 
                    <React.Fragment>
                        <FormStyle width={width} type={type}>
                            <label>{label}</label>
                            <select className={data===placeholder || data==="" ? `placeholder ${error_style}` : error_style} value={this.state.data} name={type} onChange={this.getData}>
                                <option value="">{placeholder}</option>
                                {options.map((op,index)=>{
                                    return (
                                        <option key={index}>{op}</option>
                                    )
                                })}
                            </select>
                        </FormStyle>
                    </React.Fragment>
                );
            break;
            case "checkbox": 
                view = ( 
                    <React.Fragment>
                        <FormStyle width={width} direction="row" justify="flex-start" type={type}>
                            <input type={type} className={error_style} value={this.state.data} name={type} onChange={this.getData}/>
                            <label>{label}</label>
                        </FormStyle>
                    </React.Fragment>
                );
            break;
            default: 
                view = ( <React.Fragment></React.Fragment> );
            break;
        }
        return (
            <React.Fragment>
                {view}
            </React.Fragment>
        )
    }
}
