import * as Types from '../types';
import API from '../../default';
import { getLoginSalt } from '../../hash';

export const LoginUser = data => {
    const { Email,Password } = data;
    const hash = getLoginSalt(Email,Password);

    const sendData = {
                "Email": Email,
                "Password": Password,
                "Hashpass": hash
            }
    return dispatch => {
        dispatch({
          type: Types.LOGIN_USER,
          payload: API({
            method: 'POST',
            url: `/?end=login`,
            data: sendData
          })
        })
    }
}

export const SignUpUser = data => {      
      
    const { Firstname, Lastname, Email , EmailConfirm, Password } = data;
    const hash = getLoginSalt(Email,Password);
    return dispatch => {
        dispatch({
          type: Types.SIGNUP_USER,
          payload: API({
            method: 'POST',
            url: `/?end=signup`,
            data: {
                "Firstname": Firstname,
                "Lastname": Lastname,
                "Email": Email,
                "EmailConfirm": EmailConfirm,
                "Password": Password,
                "hashpass": hash
            }
          })
        })
    }
}

export const Logout = () => {
    localStorage.removeItem("ax_auth");
    return dispatch => {
        dispatch({
                type: Types.LOGOUT_USER
            });
    }
}

