import React from 'react';
import { SocialButtonStyle } from '../../styles/init';

/**
 * @param string width in px
 * @param string height in px
 * @param string bgcolor e.g #F5F6V6
 * @param string icon 
 * @param string text 
 */

export const SocialButton = (props) => {
    const { width,height,bgcolor,icon,text, onclick } = props;
    return (
        <SocialButtonStyle width={width} height={height} bgcolor={bgcolor} onClick={onclick}>
            <img src={icon} alt={text} />
            <p>{text}</p>
        </SocialButtonStyle>
    )
}

export default SocialButton;